var uz = {
  modules : [],
  init : function(){
    this.modules.forEach(function(item, i){
      if(item.hasOwnProperty('init')){
        item.init(item);
      }
    });
  },
  ready : function(){
    this.modules.forEach(function(item, i){
      if(item.hasOwnProperty('ready')){
        item.ready(item);
      }
    });
  },
  scroll : function(){
    var self = this;
      self.modules.forEach(function(item, i){
        if(item.hasOwnProperty('scroll')){
          item.scroll(item);
        }
      });
  },
  resize : function(){
    this.modules.forEach(function(item, i){
      if(item.hasOwnProperty('resize')){
        item.resize(item);
      }
    });
  }
}

uz.modules.push(require('./pages/home'));
uz.modules.push(require('./pages/community/super-saturday'));
uz.modules.push(require('./collections/home-landing'));
uz.modules.push(require('./collections/accessories'));

uz.init(uz);
document.addEventListener('DOMContentLoaded', uz.ready.bind(uz));
document.addEventListener('scroll', uz.scroll.bind(uz));
window.addEventListener('resize', uz.resize.bind(uz));