'use strict';

var $ = require('jquery');
var VimeoPlayer = require('@vimeo/player');

var home = {
  init: function(){
  },

  ready: function(){
    if ($('.video-item').length){
      this.video.init();
    }
  },
  
  resize:function(){
    
  },  
  scroll: function(){
    
  },
  video: { 
    vid : null,
    init: function(){
      var self = this;
      $('.video-item').click(function(){
        var options = {
          id: $(this).attr('data-video-id'),
          width: 1200,
          loop: false
        }; 
        self.vid = new VimeoPlayer('video', options);
        self.vid.loadVideo($(this).attr('data-video-id'));
        $('.video-overlay').addClass('video-open');
        $('body').addClass('lock-scroll');
        
      });
      $('.video-overlay').click(function(){
        self.vid.pause();
        $('.video-overlay').removeClass('video-open');
        $('body').removeClass('lock-scroll');
      });
      $('.video-overlay #video iframe').click(function(e){
        e.stopPropagation();
      });
    },
  }
};
module.exports = home;